<template>
  <div class="biz-content">
    <div class="data">
      <el-row :gutter="10">
        <el-col :span="6">
          <el-collapse v-model="activeName" accordion>
            <el-collapse-item
              v-for="item in itemList"
              :key="item.name"
              :title="item.title"
              :name="item.name">
              <div class="grid-content bg-purple dimension-dialog">
                <div class="dimension">
                  <div class="item" v-for="d in item.detail" :key="d.label" @click="addTag(d.label)">
                    <span>{{d.label}}</span>
                  </div>
                </div>
              </div>
            </el-collapse-item>
          </el-collapse>
        </el-col>
        <el-col :span="18">
          <div class="grid-content bg-purple-dark">
            <el-table border size="mini" class="tb-edit" :data="bizData" highlight-current-row>
              <el-table-column prop="title" label="业务小类名称" width="150">
                <template slot-scope="scope">
                  <el-input
                    size="small"
                    v-if="scope.row.isEdit"
                    style="width: 100%"
                    v-model="scope.row.bizName"
                    placeholder="业务小类名称">
                  </el-input>
                  <span v-else>{{scope.row.bizName}}</span>
                </template>
              </el-table-column>
              <el-table-column prop="tags" label="业务小类范围">
                <template slot-scope="scope">
                  <el-tag
                    disable-transitions size="small"
                    :closable="scope.row.isEdit"
                    v-for="tag in scope.row.tags"
                    @close="removeTag(tag)"
                    :key="tag">
                    {{tag}}
                  </el-tag>
                </template>
              </el-table-column>
              <el-table-column
                fixed="right"
                label="操作"
                width="100">
                <template slot-scope="scope">
                  <el-tooltip class="item" effect="dark" content="编辑" placement="top">
                    <el-button
                      size="small"
                      type="text"
                      @click="edit(scope.row.id)"
                      icon="iconfont icon-bianji"
                      v-show=" currentEdit!== scope.row.id">
                    </el-button>
                  </el-tooltip>
                  <el-tooltip class="item" effect="dark" content="保存" placement="top">
                    <el-button
                      size="small"
                      type="text"
                      @click="save"
                      style="margin-left: 0;"
                      icon="iconfont icon-fuzhi"
                      v-show="currentEdit === scope.row.id">
                    </el-button>
                  </el-tooltip>
                  <el-tooltip class="item" effect="dark" content="删除" placement="top">
                    <el-button
                      @click.native.prevent="remove(scope.row.id)"
                      type="text"
                      size="small"
                      icon="iconfont icon-iconfont-shanchu"
                      style="color: #FE2B6F">
                    </el-button>
                  </el-tooltip>
                </template>
              </el-table-column>
            </el-table>
            <el-button
              :style="specialTableBtn"
              type="primary"
              icon="el-icon-plus"
              @click="addRecord">
              新增行
            </el-button>
          </div>
        </el-col>
      </el-row>
    </div>
    <div class="option">
      <back-button plain>上一步</back-button>
      <back-button plain type="next" @next="config">下一步</back-button>
    </div>
  </div>
</template>

<script>
import {generateUUID} from '@/utils/uuid'
const BackButton = () => import('@/components/BackButton')
export default {
  name: 'CustomTwo',
  components: {BackButton},
  data () {
    return {
      itemList: [
        {title: '年龄', name: 'age', detail: [{label: '20以下'}, {label: '20-30'}, {label: '31-45'}, {label: '46-60'}, {label: '60以上'}]},
        {title: '性别', name: 'gender', detail: [{label: '男'}, {label: '女'}]},
        {title: '使用性质', name: 'usage', detail: [{label: '家庭自用'}, {label: '营业出租租赁'}, {label: '营业旅游'}, {label: '营业货车'}, {label: '非营业机关'}, {label: '非营业企业'}]},
        {title: '商业险上年出险金额', name: 'rci', detail: [{label: '未投保商业险'}, {label: '0-1000'}, {label: '1000-2000'}, {label: '2000-5000'}, {label: '5000-10000'}, {label: '10000+'}]},
        {title: '新车购置价', name: 'car-price', detail: [{label: '0-10万'}, {label: '10-25万'}, {label: '25-40万'}, {label: '40万以上'}]},
        {title: '车龄', name: 'car-age', detail: [{label: '0'}, {label: '1-3'}, {label: '3-6'}, {label: '6-10'}, {label: '10年以上'}]}
      ],
      bizData: [
        {id: '1', bizName: '家庭自用少年', isEdit: false, tags: ['20以下', '男', '家庭自用']}
      ],
      specialTableBtn: {
        width: '100%',
        marginTop: '5px',
        border: '1px dashed #15afff',
        background: '#fff',
        color: '#15afff',
        display: 'block'
      },
      currentEdit: '',
      activeName: ''
    }
  },
  computed: {
    currentEditIndex () {
      return this.bizData.findIndex(item => item.id === this.currentEdit)
    }
  },
  methods: {
    edit (id) {
      if (this.currentEditIndex > -1) {
        this.bizData[this.currentEditIndex].isEdit = false
      }
      this.currentEdit = id
      this.bizData[this.currentEditIndex].isEdit = true
    },
    save () {
      this.bizData[this.currentEditIndex].isEdit = false
      this.currentEdit = ''
    },
    remove (id) {
      const index = this.bizData.findIndex(item => item.id === id)
      if (index === this.currentEditIndex) {
        this.currentEdit = ''
      }
      this.bizData.splice(index, 1)
    },
    addRecord () {
      const id = generateUUID()
      const newItem = {id: id, bizName: '', isEdit: true, tags: []}
      this.bizData.push(newItem)
      this.edit(id)
    },
    removeTag (name) {
      const tags = this.bizData[this.currentEditIndex].tags
      const index = tags.findIndex(item => item === name)
      this.bizData[this.currentEditIndex].tags.splice(index, 1)
    },
    addTag (name) {
      if (this.currentEditIndex > -1) {
        const tags = this.bizData[this.currentEditIndex].tags
        const index = tags.findIndex(item => item === name)
        if (index === -1) {
          this.bizData[this.currentEditIndex].tags.push(name)
        }
      }
    },
    config () {
      this.$router.push({path: '/admin/agency/config/customDisFee/three'})
    }
  }
}
</script>

<style scoped>
  .dimension-dialog > .dimension {
    float: left;
    width: 98%;
    border-bottom: solid 1px #eee;
    border-left: solid 1px #eee;
  }

  .dimension-dialog > .dimension > .item {
    width: 100%;
    height: 47px;
    font-size:12px;
    line-height: 47px;
    border-top: solid 1px #eee;
    border-right: solid 1px #eee;
    cursor: pointer;
    position: relative;
  }
  .dimension-dialog > .dimension > .item:hover{
    background:#F4FAFF;
    font-size:14px;
    border:1px solid #15afff
  }
  .dimension-dialog > .dimension > .item > span {
    padding-left: 10px;
  }
</style>



